.profile__foto {
  border: 1px solid rgba(255, 255, 255, 0.372);
  border-radius: 50%;
  background-color: rgba(72, 147, 251, 0.337);
}

.sobre-mim {
  padding-top: 50px;
  max-width: 1200px;
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-areas:
    'tittle tittle'
    'description skills';
  grid-template-rows: auto 1fr;
  grid-template-columns: 1fr 1fr;
  gap: 64px;
}

.description {
  grid-area: description;
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.description__tittle {
  font-size: 48px;
  color: var(--primary-color);
}

.description__text {
  font-size: 1.5rem;
  width: 85%;
  line-height: 150%;
  text-align: justify;
}

.skills {
  grid-area: skills;
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.skills__tittle {
  font-size: 48px;
  color: var(--primary-color);
}

.skills__container {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}

.skills__icon {
  width: 128px;
  height: 128px;
  background-color: var(--icon-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 12px;
}

.skills__icon i {
  font-size: 2rem;
}

.skills__icon span {
  font-weight: 700;
  font-size: 1rem;
}
